  transform: rotateZ(180deg);
}

.navds-accordion__header {
  padding: var(--navds-spacing-4) var(--navds-spacing-3);
  background: transparent;
  text-align: left;
  border: none;
  width: 100%;
  margin: 0;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
