  color: var(--navds-button-color-primary-text);
}

.navds-button--primary:hover {
  background-color: var(--navds-button-color-primary-background-hover);
}

.navds-button--primary:active {
  background-color: var(--navds-button-color-primary-background-active);
}

.navds-button--primary:focus {
  box-shadow: inset 0 0 0 1px var(--navds-button-color-primary-border-focus),
    var(--navds-shadow-focus);
}

.navds-button--primary:hover:disabled,
.navds-button--primary:active:disabled {
  background-color: var(--navds-button-color-primary-background);
}

/**************************
* .navds-button--secondary *
 **************************/

.navds-button--secondary {
  color: var(--navds-button-color-secondary-text);
  background-color: var(--navds-button-color-secondary-background);
  box-shadow: inset 0 0 0 2px var(--navds-button-color-secondary-border);
}

.navds-button--secondary:hover {
  color: var(--navds-button-color-secondary-text-hover);
  background-color: var(--navds-button-color-secondary-background-hover);
  box-shadow: none;
}

.navds-button--secondary:focus {
  box-shadow: inset 0 0 0 2px var(--navds-button-color-secondary-border),
    var(--navds-shadow-focus);
}

.navds-button--secondary:active {
  color: var(--navds-button-color-secondary-text-active);
  background-color: var(--navds-button-color-secondary-background-active);
  box-shadow: none;
}

.navds-button--secondary:focus:hover,
.navds-button--secondary:focus:active {
  box-shadow: inset 0 0 0 1px
      var(--navds-button-color-secondary-border-focus-active-hover),
    var(--navds-shadow-focus);
}

.navds-button--secondary:disabled,
.navds-button--secondary:hover:disabled {
  color: var(--navds-button-color-secondary-text);
  background-color: var(--navds-button-color-secondary-background);
  box-shadow: inset 0 0 0 2px var(--navds-button-color-secondary-border);
}

/****************************
 * .navds-button--tertiary *
 ****************************/

.navds-button--tertiary {
  color: var(--navds-button-color-tertiary-text);
}

.navds-button--tertiary:hover {
  box-shadow: inset 0 0 0 2px var(--navds-button-color-tertiary-border-hover);
}

.navds-button--tertiary:focus {
  box-shadow: inset 0 0 0 2px var(--navds-button-color-tertiary-border-focus),
    var(--navds-shadow-focus);
}

.navds-button--tertiary:active {
  color: var(--navds-button-color-tertiary-text-active);
  background-color: var(--navds-button-color-tertiary-background-active);
  box-shadow: inset 0 0 0 1px var(--navds-button-color-tertiary-border-active);
}

.navds-button--tertiary:active:hover {
  background-color: var(--navds-button-color-tertiary-background-active);
}

.navds-button--tertiary:active:focus {
  box-shadow: inset 0 0 0 1px var(--navds-button-color-tertiary-border-active),
    var(--navds-shadow-focus);
}

.navds-button--tertiary:disabled,
.navds-button--tertiary:active:disabled,
.navds-button--tertiary:active:hover:disabled {
  color: var(--navds-button-color-tertiary-text);
  background: none;
  box-shadow: none;
}

/*************************
 * .navds-button--danger *
 *************************/

.navds-button--danger {
  background-color: var(--navds-button-color-danger-background);
  color: var(--navds-button-color-danger-text);
}

.navds-button--danger:hover {
  background-color: var(--navds-button-color-danger-background-hover);
}

.navds-button--danger:active {
  background-color: var(--navds-button-color-danger-background-active);
}

.navds-button--danger:focus {
  box-shadow: inset 0 0 0 1px var(--navds-button-color-danger-border-focus),
    var(--navds-shadow-focus);
}

.navds-button--danger:hover:disabled,
.navds-button--danger:active:disabled {
  background-color: var(--navds-button-color-danger-background);
}

/**************************
 * .navds-button:disabled *
 **************************/

.navds-button:disabled {
  cursor: not-allowed;
}

.navds-button:disabled:not(.navds-button--loading) {
  opacity: 0.3;
}

.navds-button .navds-loader .navds-loader__foreground {
  stroke: var(--navds-loader-color-on-button-foreground);
}

.navds-button--primary .navds-loader .navds-loader__background,
.navds-button--danger .navds-loader .navds-loader__background {
  stroke: var(--navds-loader-color-on-button-background);
}

.navds-content-container {
  margin-left: auto;
  margin-right: auto;
  max-width: 79.5rem;
  padding: var(--navds-spacing-4);
}

@media (min-width: 448px) {
  .navds-content-container {
    padding: var(--navds-spacing-6);
  }
}

:root {
  --navds-guide-panel-color-background: var(
    --navds-semantic-color-component-background-light
  );
  --navds-guide-panel-color-border: var(--navds-global-color-blue-400);
  --navds-guide-panel-color-illustration-background: var(
    --navds-global-color-blue-200
  );
}

/**
 * GuidePanel component
 */

.navds-guide-panel {
  position: relative;
  padding-left: 2.5rem;
}

.navds-guide-panel__content {
  background-color: var(--navds-guide-panel-color-background);
  border-radius: 4px;
  border: 2px solid var(--navds-guide-panel-color-border);
  min-height: 7.25rem;
  padding: 1.5rem;
  padding-left: 3.5rem;
}

.navds-guide-panel--poster {
  padding-left: 0;
  padding-top: 3.125rem;
}

.navds-guide-panel--poster .navds-guide-panel__content {
  padding: 2rem;
  padding-top: 4.25rem;
}

.navds-guide-panel .navds-guide {
  position: absolute;
  top: 1rem;
  transform: translateX(-50%);
}

.navds-guide-panel--poster .navds-guide {
  left: 50%;
  top: 0;
}

/**
 * Guide component
 */

.navds-guide {
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Guide illustration frame */

.navds-guide__illustration {
  background: var(--navds-guide-panel-color-illustration-background);
  border-radius: 50%;
  overflow: hidden;
}

.navds-guide__illustration svg,
.navds-guide__illustration img {
  height: 100%;
  width: 100%;
}

/* Illustration sizes */

.navds-guide__illustration--small {
  height: 5rem;
  width: 5rem;
}

.navds-guide__illustration--medium {
  height: 6.25rem;
  width: 6.25rem;
}

/*
  Order matters; rearrange with care
*/

.navds-fieldset {
  margin: 0;
  padding: 0;
  border: 0;
  min-width: 0;
}

.navds-fieldset > :not(:first-child):not(:empty) {
  margin-top: var(--navds-spacing-2);
}

.navds-fieldset > .navds-fieldset__description:not(:empty) {
  margin-top: var(--navds-spacing-1);
}

/* Applied when hideLegend is applied to fieldset */

.navds-fieldset > .sr-only + :not(:first-child):not(:empty) {
  margin-top: 0;
}

.navds-fieldset:disabled > .navds-fieldset__legend,
.navds-fieldset:disabled > .navds-fieldset__description {
  opacity: 0.3;
}

.navds-form-field > :not(:first-child):not(:empty),
.navds-form-field > input:not(:first-child) {
  margin-top: var(--navds-spacing-2);
}

/* Applied when hideLabel is applied to form-element */

.navds-form-field > .sr-only + :not(:first-child):not(:empty),
.navds-form-field > .sr-only + input:not(:first-child) {
  margin-top: 0;
}

:root {
  --navds-error-summary-color-background: var(
    --navds-semantic-color-component-background-light
  );
  --navds-error-summary-color-border: var(
    --navds-semantic-color-feedback-danger-border
  );
}

.navds-error-summary {
  background-color: var(--navds-error-summary-color-background);
  padding: var(--navds-spacing-4);
  border: 4px solid var(--navds-error-summary-color-border);
}

.navds-error-summary--small {
  padding: var(--navds-spacing-2);
}

.navds-error-summary:focus {
  box-shadow: var(--navds-shadow-focus);
  outline: none;
}

.navds-error-summary__list {
  margin: 1rem 0;
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.navds-error-summary--small > .navds-error-summary__list {
  margin: 0.5rem 0;
}

:root {
  --navds-confirmation-panel-color-background: var(
    --navds-semantic-color-feedback-warning-background
  );
  --navds-confirmation-panel-color-background-checked: var(
    --navds-semantic-color-feedback-success-background
  );
  --navds-confirmation-panel-color-border-checked: var(
    --navds-semantic-color-feedback-success-background
  );
  --navds-confirmation-panel-color-background-error: var(
    --navds-semantic-color-feedback-danger-background
  );
  --navds-confirmation-panel-color-checkbox-background-checked-hover: var(
    --navds-global-color-green-700
  );
  --navds-confirmation-panel-color-checkbox-border-checked-hover: var(
    --navds-global-color-green-700
  );
  --navds-confirmation-panel-color-checkbox-background-checked: var(
    --navds-global-color-green-500
  );
  --navds-confirmation-panel-color-checkbox-border-checked: var(
    --navds-semantic-color-feedback-success-border
  );
  --navds-confirmation-panel-color-checkbox-label-checked: var(
    --navds-semantic-color-text
  );
}

.navds-confirmation-panel {
  display: flex;
  flex-direction: column;
  gap: var(--navds-spacing-4);
  padding: var(--navds-spacing-4);
  border-radius: 4px;
  background-color: var(--navds-confirmation-panel-color-background);
  transition: background-color linear 100ms;
}

.navds-confirmation-panel__content {
  max-width: 80ch;
}

.navds-confirmation-panel--small {
  padding: var(--navds-spacing-3);
  gap: var(--navds-spacing-2);
}

.navds-confirmation-panel--checked {
  background-color: var(--navds-confirmation-panel-color-background-checked);
  border-color: var(--navds-confirmation-panel-color-border-checked);
}

.navds-confirmation-panel--error {
  background-color: var(--navds-confirmation-panel-color-background-error);
  border: 1px solid transparent;
}

.navds-confirmation-panel
  .navds-checkbox__input:hover
  + .navds-checkbox__label {
  text-decoration: underline;
}

.navds-confirmation-panel--checked
  .navds-checkbox__input:hover
  + .navds-checkbox__label {
  color: var(--navds-confirmation-panel-color-checkbox-label-checked);
}

.navds-confirmation-panel--checked
  .navds-checkbox__input
  + .navds-checkbox__label::before {
  transition: none;
  background-color: var(
    --navds-confirmation-panel-color-checkbox-background-checked
  );
  border-color: var(--navds-confirmation-panel-color-checkbox-border-checked);
  background-image: url(data:image/svg+xml;base64,);
}

.navds-confirmation-panel--checked
  .navds-checkbox__input:hover
  + .navds-checkbox__label::before {
  background-color: var(
    --navds-confirmation-panel-color-checkbox-background-checked-hover
  );
  border-color: var(
    --navds-confirmation-panel-color-checkbox-border-checked-hover
  );
}

.navds-confirmation-panel--checked
  .navds-checkbox__input:focus:hover
  + .navds-checkbox__label::before {
  background-image: url(data:image/svg+xml;base64,);
}

:root {
  --navds-error-message-color-text: var(
    --navds-semantic-color-feedback-danger-text
  );
}

.navds-error-message {
  color: var(--navds-error-message-color-text);
  display: flex;
  gap: var(--navds-spacing-2);
}

.navds-error-message::before {
  content: "";
}

:root {
  --navds-radio-checkbox-color-background: var(
    --navds-semantic-color-canvas-background-light
  );
  --navds-radio-checkbox-color-background-hover: var(
    --navds-semantic-color-interaction-primary-hover-subtle
  );
  --navds-radio-checkbox-color-background-checked: var(
    --navds-semantic-color-interaction-primary
  );
  --navds-radio-checkbox-color-shadow: var(--navds-semantic-color-border);
  --navds-radio-checkbox-color-shadow-hover: var(
    --navds-semantic-color-interaction-primary
  );
  --navds-radio-checkbox-color-shadow-checked: var(
    --navds-semantic-color-interaction-primary
  );
  --navds-radio-checkbox-color-shadow-error: var(
    --navds-semantic-color-interaction-danger
  );
  --navds-radio-checkbox-color-label-hover: var(
    --navds-semantic-color-interaction-primary
  );
}

.navds-checkbox,
.navds-radio {
  position: relative;
  width: fit-content;
}

.navds-checkbox__input,
.navds-radio__input {
  position: absolute;
  width: 48px;
  height: 48px;
  top: 0;
  left: -12px;
  z-index: 1;
  opacity: 0;
  cursor: pointer;
}

.navds-checkbox__label,
.navds-radio__label {
  padding: var(--navds-spacing-3) 0;
  cursor: pointer;
  display: flex;
  gap: var(--navds-spacing-2);
}

.navds-checkbox__label::before,
.navds-radio__label::before {
  content: "";
  background-color: var(--navds-radio-checkbox-color-background);
  box-shadow: inset 0 0 0 2px var(--navds-radio-checkbox-color-shadow);
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 4px;
  flex-shrink: 0;
}

.navds-radio__label::before {
  border-radius: 50%;
}

.navds-checkbox__content,
.navds-radio__content {
  display: flex;
  flex-direction: column;
  gap: var(--navds-spacing-1);
}

.navds-checkbox__description,
.navds-radio__description {
  color: var(--navds-color-text-primary);
}

.navds-checkbox--small > .navds-checkbox__input,
.navds-radio--small > .navds-radio__input {
  width: 32px;
  height: 32px;
  top: 0;
  left: -6px;
}

.navds-checkbox--small > .navds-checkbox__label,
.navds-radio--small > .navds-radio__label {
  padding: 6px 0;
}

.navds-checkbox--small > .navds-checkbox__label::before,
.navds-radio--small > .navds-radio__label::before {
  width: 1.25rem;
  height: 1.25rem;
}

.navds-checkbox__input:focus + .navds-checkbox__label::before,
.navds-radio__input:focus + .navds-radio__label::before {
  box-shadow: inset 0 0 0 2px var(--navds-radio-checkbox-color-shadow),
    var(--navds-shadow-focus);
}

.navds-checkbox__input:hover:focus + .navds-checkbox__label::before,
.navds-radio__input:hover:focus + .navds-radio__label::before {
  box-shadow: inset 0 0 0 2px var(--navds-radio-checkbox-color-shadow-hover),
    var(--navds-shadow-focus);
}

.navds-checkbox__input:checked + .navds-checkbox__label::before {
  background-image: url(data:image/svg+xml;base64,);
  background-position: 6px center;
  background-repeat: no-repeat;
  background-size: 13px;
  box-shadow: none;
  background-color: var(--navds-radio-checkbox-color-background-checked);
}

.navds-checkbox--small
  > .navds-checkbox__input:checked
  + .navds-checkbox__label::before {
  background-position: 4px center;
}

.navds-checkbox__input:checked:focus + .navds-checkbox__label::before {
  box-shadow: inset 0 0 0 1px white, var(--navds-shadow-focus);
}

.navds-radio__input:checked + .navds-radio__label::before {
  box-shadow: inset 0 0 0 2px var(--navds-radio-checkbox-color-shadow-checked),
    inset 0 0 0 4px #fff;
  background-color: var(--navds-radio-checkbox-color-background-checked);
}

.navds-radio__input:checked:focus + .navds-radio__label::before {
  box-shadow: inset 0 0 0 2px var(--navds-radio-checkbox-color-shadow-checked),
    inset 0 0 0 4px #fff, var(--navds-shadow-focus);
}

.navds-checkbox__input:hover:not(:disabled) + .navds-checkbox__label,
.navds-radio__input:hover:not(:disabled) + .navds-radio__label {
  color: var(--navds-radio-checkbox-color-label-hover);
}

.navds-checkbox__input:hover:not(:disabled):not(:checked):not(:focus)
  + .navds-checkbox__label::before,
.navds-radio__input:hover:not(:disabled):not(:checked):not(:focus)
  + .navds-radio__label::before {
  box-shadow: inset 0 0 0 2px var(--navds-radio-checkbox-color-shadow-hover);
  background-color: var(--navds-radio-checkbox-color-background-hover);
}

.navds-checkbox--error
  > .navds-checkbox__input:not(:hover):not(:disabled):not(:checked)
  + .navds-checkbox__label::before,
.navds-radio--error
  > .navds-radio__input:not(:hover):not(:disabled):not(:checked)
  + .navds-radio__label::before {
  box-shadow: inset 0 0 0 2px var(--navds-radio-checkbox-color-shadow-error);
}

.navds-checkbox--error
  > .navds-checkbox__input:focus:not(:hover):not(:disabled):not(:checked)
  + .navds-checkbox__label::before,
.navds-radio--error
  > .navds-radio__input:focus:not(:hover):not(:disabled):not(:checked)
  + .navds-radio__label::before {
  box-shadow: inset 0 0 0 2px var(--navds-radio-checkbox-color-shadow-error),
    var(--navds-shadow-focus);
}

.navds-checkbox--disabled,
.navds-radio--disabled {
  opacity: 0.3;
}

.navds-checkbox--disabled > .navds-checkbox__input,
.navds-checkbox--disabled > .navds-checkbox__label,
.navds-radio--disabled > .navds-radio__input,
.navds-radio--disabled > .navds-radio__label {
  cursor: not-allowed;
}

:root {
  --navds-select-color-background: var(
    --navds-semantic-color-component-background-light
  );
  --navds-select-color-border: var(--navds-semantic-color-border);
  --navds-select-color-border-hover: var(
    --navds-semantic-color-interaction-primary
  );
  --navds-select-color-border-error: var(
    --navds-semantic-color-interaction-danger
  );
  --navds-select-color-shadow-error: var(
    --navds-semantic-color-interaction-danger
  );
  --navds-select-color-border-disabled: var(--navds-global-color-gray-400);
  --navds-select-color-background-disabled: var(
    --navds-semantic-color-component-background-alternate
  );
  --navds-select-color-text-disabled: var(--navds-semantic-color-text);
}

.navds-select__input {
  appearance: none;
  background-color: var(--navds-select-color-background);
  border-radius: 4px;
  border: 1px solid var(--navds-select-color-border);
  width: 100%;
  box-sizing: border-box;
  min-height: 48px;
  display: inline-block;
  position: relative;
  padding: 0.5rem;
  padding-right: 2.5rem;
}

.navds-select__input:hover {
  border-color: var(--navds-select-color-border-hover);
}

.navds-select__input:focus {
  outline: none;
  box-shadow: var(--navds-shadow-focus);
}

.navds-select__container {
  position: relative;
  display: flex;
  width: 100%;
  align-items: center;
}

.navds-select__chevron {
  position: absolute;
  font-size: 1rem;
  right: 0.5rem;
  pointer-events: none;
}

.navds-form-field--small .navds-select__input {
  min-height: 32px;
  padding: 0.25rem;
  padding-right: 2rem;
}

/**
  Error handling
*/

.navds-select--error > * select:not(:hover):not(:focus):not(:disabled) {
  box-shadow: 0 0 0 1px var(--navds-select-color-shadow-error);
  border-color: var(--navds-select-color-border-error);
}

/*
  Disabled handling
 */

.navds-select--disabled {
  opacity: 0.3;
}

.navds-select__input:disabled {
  background-color: var(--navds-select-color-background);
  border-color: var(--navds-select-color-border);
  box-shadow: none;
  cursor: not-allowed;
}

:root {
  --navds-switch-color-label: var(--navds-semantic-color-text);
  --navds-switch-color-label-hover: var(
    --navds-semantic-color-interaction-primary
  );
  --navds-switch-color-track-background: var(--navds-semantic-color-text-muted);
  --navds-switch-color-track-background-checked: var(
    --navds-semantic-color-feedback-success-border
  );
  --navds-switch-color-track-background-hover: var(
    --navds-global-color-gray-800
  );
  --navds-switch-color-track-background-hover-checked: var(
    --navds-semantic-color-feedback-success-icon
  );
  --navds-switch-color-track-shadow-inner-focus: var(
    --navds-semantic-color-component-background-light
  );
  --navds-switch-color-thumb: var(
    --navds-semantic-color-component-background-light
  );
  --navds-switch-color-thumb-icon: var(--navds-semantic-color-text-muted);
  --navds-switch-color-thumb-icon-checked: var(
    --navds-semantic-color-feedback-success-border
  );
  --navds-switch-color-thumb-loader-checked: var(
    --navds-semantic-color-feedback-success-border
  );
}

.navds-switch {
  position: relative;
  min-height: 48px;
  width: fit-content;
}

.navds-switch--right {
  width: auto;
}

.navds-switch--small {
  position: relative;
  min-height: 32px;
}

/* Input */

.navds-switch__input {
  cursor: pointer;
  position: absolute;
  z-index: 1;
  width: 48px;
  height: 48px;
  opacity: 0;
  top: 0;
}

.navds-switch--small > .navds-switch__input {
  height: 32px;
  top: 0;
}

/* Label */

.navds-switch__label-wrapper {
  cursor: pointer;
  color: var(--navds-switch-color-label);
}

.navds-switch__content {
  display: flex;
  flex-direction: column;
  gap: var(--navds-spacing-1);
  padding: 0.75rem 0 0.75rem 3.25rem;
}

.navds-switch--right > .navds-switch__label-wrapper > .navds-switch__content {
  padding: 0.75rem 3.25rem 0.75rem 0;
}

.navds-switch--small > .navds-switch__label-wrapper > .navds-switch__content {
  padding: calc(var(--navds-spacing-2) - 2px) 0
    calc(var(--navds-spacing-2) - 2px) 3.25rem;
}

.navds-switch--right.navds-switch--small
  > .navds-switch__label-wrapper
  > .navds-switch__content {
  padding: calc(var(--navds-spacing-2) - 2px) 3.25rem
    calc(var(--navds-spacing-2) - 2px) 0;
}

.navds-switch--with-description,
.navds-switch--small
  > .navds-switch__label-wrapper
  > .navds-switch--with-description {
  padding-bottom: 0;
}

.navds-switch__input:hover ~ .navds-switch__label-wrapper,
.navds-switch__label-wrapper:hover {
  color: var(--navds-switch-color-label-hover);
}

.navds-switch__input:disabled:hover ~ .navds-switch__label-wrapper {
  color: inherit;
}

/* Track */

.navds-switch__track {
  width: 44px;
  height: 24px;
  background-color: var(--navds-switch-color-track-background);
  position: absolute;
  top: var(--navds-spacing-3);
  left: 0;
  border-radius: 4px;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.navds-switch--small > .navds-switch__track {
  top: var(--navds-spacing-1);
}

.navds-switch--right > .navds-switch__input,
.navds-switch--right > .navds-switch__track {
  right: 0;
  left: auto;
}

.navds-switch__input:checked ~ .navds-switch__track {
  background-color: var(--navds-switch-color-track-background-checked);
}

.navds-switch__input:hover ~ .navds-switch__track {
  background-color: var(--navds-switch-color-track-background-hover);
}

.navds-switch__input:hover:checked ~ .navds-switch__track {
  background-color: var(--navds-switch-color-track-background-hover-checked);
}

.navds-switch__input:disabled ~ .navds-switch__track {
  background-color: var(--navds-switch-color-track-background);
}

.navds-switch__input:checked:disabled ~ .navds-switch__track {
  background-color: var(--navds-switch-color-track-background-checked);
}

.navds-switch__input:focus ~ .navds-switch__track {
  box-shadow: 0 0 0 1px var(--navds-switch-color-track-shadow-inner-focus),
    var(--navds-shadow-focus);
}

/* Thumb */

.navds-switch__thumb {
  background-color: var(--navds-switch-color-thumb);
  color: var(--navds-switch-color-thumb-icon);
  border-radius: 2.5px;
  width: 20px;
  height: 20px;
  position: absolute;
  transform: translateX(0);
  left: 2px;
  top: 2px;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  display: flex;
  align-items: center;
  justify-content: center;
}

.navds-switch__input:checked ~ .navds-switch__track > .navds-switch__thumb {
  transform: translateX(20px);
  color: var(--navds-switch-color-thumb-icon-checked);
}

@media (hover: hover) and (pointer: fine) {
  .navds-switch__input:hover ~ .navds-switch__track > .navds-switch__thumb {
    transform: translateX(2px);
  }

  .navds-switch__input:checked:hover
    ~ .navds-switch__track
    > .navds-switch__thumb {
    transform: translateX(18px);
  }
}

.navds-switch__input:disabled:hover
  ~ .navds-switch__track
  > .navds-switch__thumb {
  transform: translateX(0);
}

.navds-switch__input:checked:disabled:hover
  ~ .navds-switch__track
  > .navds-switch__thumb {
  transform: translateX(20px);
}

/* Loader */

.navds-switch__input:checked
  ~ .navds-switch__track
  > .navds-switch__thumb
  .navds-loader__foreground {
  stroke: var(--navds-switch-color-thumb-loader-checked);
}

/* Disabled */

.navds-switch__input:disabled {
  appearance: none;
}

.navds-switch--disabled {
  opacity: 0.3;
}

.navds-switch__input:disabled,
.navds-switch__input:disabled ~ .navds-switch__label-wrapper {
  cursor: not-allowed;
}

